@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
h1{
  font-size: 10px;
}
.tem-container{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 0 10px;
  
}
.tem-normal{
  
  border-radius: 20px;
  padding:  10px 0 0 10px;
  overflow: auto;
  position: relative;
  -webkit-animation:  1s cubic-bezier(1,-0.01, 0, 1) 0s 1 move-up;
          animation:  1s cubic-bezier(1,-0.01, 0, 1) 0s 1 move-up;
}

.tem-container a{
    text-decoration: none;
    width: calc(100% * (1/2) - 5px);
    
    
}
.tem-container a:nth-child(even){
  margin: 0 0 10px 10px;
}

.tem-number{
  border-radius: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 0;
  color: #fff;
}
  

  .titulo{
    display: flex;
    justify-content: space-between;
    padding: 0 10px 0 0;
    margin-bottom: 10px;
  }
  .titulo span{
    font-weight: 700;
    font-size: 18px;
    color: rgba(0, 0, 0, .2);
  }
  .tem-normal-info{
    display: flex;
    justify-content: space-between;
    
  }
  .tem-normal-info > div:nth-child(2){
    display: flex;
    background: rgba(0, 0, 0, .2);
    border-radius: 50%;

  }
  .tem-imagen{
    width: 100%;
  }
  .tem-normal p, .tem-normal h2{
    margin: 0;
    font-size: 16px;
    color:white
    
  }

.tipo-icono{
  width: 100%;
  max-width: 20px;
  min-width: 15px;
  }

  .tipos{
    display: flex;
    flex-direction: column;
    margin-right: 5px;
  }
  .tipos > div{
    display: flex;
    background: rgba(0, 0, 0, .2);
    margin-bottom: 5px;
    border-radius: 10px;
    padding: 0 5px;
    justify-content: space-between;
    align-items: center;
    min-width: 65px;
    max-width: 40px;
    
  }
  .tipos p{
    font-size: 12px;
  }

