.boton-filtro{
    border: none;
    width: calc(100% * (1/7) - 5px);
    border-radius: 5px;
    color: #000;
}
button:focus{
    outline: none;
}

.boton-filtro div{
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 0;
}
.boton-filtro div img{
    max-width: 20px;
    max-height: inherit;
    width: 100%;
}
.boton-filtro div p{
    margin: 0;
}
.tipos-container {
    padding: 0 15px;
    margin-top: 15px;
    justify-content: space-between;
    display: flex;
    flex-wrap: wrap;

  }

.tipos-container button{
    margin: 0 0 5px 5px;
}
.tipos-container button:nth-child(1){
    margin: 0 0 5px 0;
}
.tipos-container button:nth-child(8){
    margin: 0 0 5px 0;
}
.header{
    background: #450278;
    box-shadow: 0px 0 3px  rgba(0, 0, 0, .3);
    padding: 0 15px 0 0;
    display: flex;
    justify-content: space-between;
    position:-webkit-sticky;
    position:sticky;
    top: 0;
    z-index: 50;
    align-items: center;

}
.boton-toggle{background:transparent;justify-content:space-between;border:none;cursor:pointer;display:flex;flex-direction:column;box-sizing:border-box;padding:15px}.boton-toggle-lineas{width:25px;height:4px;background:#fff;border-radius:5px;margin-bottom:2px}.sidebar{height:100%;background:#2C3E50;position:fixed;top:0;left:0;width:70%;z-index:200;-webkit-transform:translateX(-100%);transform:translateX(-100%);transition:-webkit-transform 0.3s cubic-bezier(1, 0, 0, 1);transition:transform 0.3s cubic-bezier(1, 0, 0, 1);transition:transform 0.3s cubic-bezier(1, 0, 0, 1), -webkit-transform 0.3s cubic-bezier(1, 0, 0, 1)}.sidebar .side-header img{width:100%}.sidebar ul{list-style:none;display:flex;flex-direction:column;justify-content:center;padding:0;margin:0}.sidebar ul li{margin:5px 0}.sidebar ul li a{color:#fff;text-decoration:none;font-size:16px;padding:15px;display:block}.sidebar ul li a:active{color:#5e82a7;background:rgba(0,0,0,0.3)}.sidebar.activo{-webkit-transform:translateX(0);transform:translateX(0)}.backdrop{position:fixed;height:100%;width:100%;top:0;left:0;background:rgba(0,0,0,0.3);z-index:100}

